 .champion-select-header {
  height: 108px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: linear-gradient(104deg, #15171b 25%, #000 35%);
}

 .champion-select-header::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 108px;
  width: 50%;
  background-image: linear-gradient(74deg, #000 30%, #15171b 50%);
}


.champion-select-header .header-keystone {
  background: #15171b;
  height: 108px;
  position: absolute;
  top: 0;
  left: 561px;
  right: 561px;
}


  .champion-select-header
  .header-keystone
  > .left-bg-section {
  height: 108px;
  width: 50px;
  position: absolute;
  left: -14px;
  background: #15171b;
  -webkit-transform: skew(14deg);
          transform: skew(14deg);
}

  .champion-select-header
  .header-keystone
  > .right-bg-section {
  height: 108px;
  width: 50px;
  position: absolute;
  right: -14px;
  background: #15171b;
  -webkit-transform: skew(-14deg);
          transform: skew(-14deg);
}

  .champion-select-header
  .header-keystone
  .header-keystone-inner {
  height: 23px;
  width: calc(100% - 2px);
  position: absolute;
  bottom: 0;
  left: 1px;
  text-align: center;
  background: transparent;
  transition: background 0.3s, opacity 0.3s;
}

  .champion-select-header
  .header-keystone
  .header-keystone-inner
  > .left-bg-section {
  height: 23px;
  width: 50px;
  position: absolute;
  left: -4px;
  background: transparent;
  transition: background 0.3s;
  -webkit-transform: skew(14deg);
          transform: skew(14deg);
}

  .champion-select-header
  .header-keystone
  .header-keystone-inner
  > .right-bg-section {
  height: 23px;
  width: 50px;
  position: absolute;
  right: -4px;
  background: transparent;
  transition: background 0.3s;
  -webkit-transform: skew(-14deg);
          transform: skew(-14deg);
}

  .champion-select-header
  .header-keystone
  .header-keystone-inner
  > .phase {
  direction: ltr;
  position: absolute;
  left: 0;
  top: 3px;
  font-size: 16px;
  letter-spacing: 0.15px;
  width: 100%;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5), -1px 1px 3px rgba(0, 0, 0, 0.5),
    -1px -1px 3px rgba(0, 0, 0, 0.5), 1px -1px 3px rgba(0, 0, 0, 0.5);
}

.red-acting
  .champion-select-header
  .header-keystone-inner {
  background: var(--right-side-color);
}

.red-acting
  .champion-select-header
  .header-keystone-inner
  .left-bg-section,
.red-acting
  .champion-select-header
  .header-keystone-inner
  .right-bg-section {
  background: var(--right-side-color);
}

.red-acting .timer-left .timer-bg {
  opacity: 0;
}

.blue-acting
  .champion-select-header
  .header-keystone-inner {
  background: var(--left-side-color);
}

.blue-acting
  .champion-select-header
  .header-keystone-inner
  .left-bg-section,
.blue-acting
  .champion-select-header
  .header-keystone-inner
  .right-bg-section {
  background: var(--left-side-color);
}

.blue-acting .timer-right .timer-bg {
  opacity: 0;
}

.show-both-timers
  .champion-select-header
