.party .summoner-wrapper {
  width: 100%;
  height: 80px;
  position: relative;
  margin-bottom: 4px;
}

 .party .summoner-wrapper:nth-child(3) {
  margin-bottom: 23px;
}


  .party
  .summoner-wrapper
  .base-color-background {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  -webkit-filter: saturate(0.6);
}


  .party
  .summoner-wrapper
  .gradient-background-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0.83) 0%,
    rgba(0, 0, 0, 0.72) 50%,
    rgba(0, 0, 0, 0.83) 100%
  );
}


  .party
  .summoner-wrapper:first-child
  .gradient-background-overlay {
  background-image: radial-gradient(
    ellipse 82% 128% at 50% 90%,
    rgba(0, 0, 0, 0.72) 0%,
    rgba(0, 0, 0, 0.91) 100%
  );
}


  .party
  .summoner-wrapper:last-child
  .gradient-background-overlay {
  background-image: radial-gradient(
    ellipse 82% 128% at 50% 10%,
    rgba(0, 0, 0, 0.72) 0%,
    rgba(0, 0, 0, 0.91) 100%
  );
}


  #blueParty
  .summoner-wrapper
  .base-color-background {
  background: var(--left-side-color);
}


  #redParty
  .summoner-wrapper
  .base-color-background {
  background: var(--right-side-color);
  }

@-webkit-keyframes flickerAnimation {
20% {
  opacity: 1;
}
60% {
  opacity: 0;
}
100% {
  opacity: 1;
}
}

@keyframes flickerAnimation {
20% {
  opacity: 1;
}
60% {
  opacity: 0;
}
100% {
  opacity: 1;
}
}

@-webkit-keyframes fadeInRightAnimation {
0% {
  opacity: 0;
  right: 23px;
}
100% {
  opacity: 1;
  right: 15px;
}
}

@keyframes fadeInRightAnimation {
0% {
  opacity: 0;
  right: 23px;
}
100% {
  opacity: 1;
  right: 15px;
}
}

@-webkit-keyframes fadeInLeftAnimation {
0% {
  opacity: 0;
  left: 23px;
}
100% {
  opacity: 1;
  left: 15px;
}
}

@keyframes fadeInLeftAnimation {
0% {
  opacity: 0;
  left: 23px;
}
100% {
  opacity: 1;
  left: 15px;
}
}

@-webkit-keyframes champLockedInAnimation {
0% {
  -webkit-filter: brightness(1);
}
4% {
  -webkit-filter: brightness(1.8);
}
100% {
  -webkit-filter: brightness(1);
}
}

@keyframes champLockedInAnimation {
0% {
  -webkit-filter: brightness(1);
}
4% {
  -webkit-filter: brightness(1.8);
}
100% {
  -webkit-filter: brightness(1);
}
}




